*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
.settings-cont{
    width: 85%;
    height: 92vh;
    display: flex;
    align-items: center;
}
.settings-cont__inner{
    width: 90%;
    height: 90%;
}
.settings-cont__inner .settings-item p:first-child{
    font-size: 22px;
}
.settings-cont__inner .settings-item p:last-child{
    text-decoration: underline;
    color: #FF8787;
}
.settings-cont__inner .settings-item{
    display: flex;
    justify-content: space-between;
    padding: 30px 100px 10px 20px;
}