*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
.friendsbar-cont{
    height: 92vh;
    width: 25%;
    overflow-y: scroll;
}


.friendsbar-cont::-webkit-scrollbar {
    width: 5px;
}
.friendsbar-cont::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.1);
}
.friendsbar-cont::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 9px #FF8787;
}




.friends-title-cont{
    margin-top: 40px;
}
.friends-title{
    text-align: center;
    font-size: 30px;
    color: #FF8787;
    font-weight: 600;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.friends-item-cont{
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.friends-item{
    margin: 10px 0;
    padding: 6px 0px;
    width: 70%;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    background-color: rgba(128, 128, 128, 0.05);
    cursor: pointer;
}
.friends-item:first-child{
    margin-top: 25px;
}