*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
.blog-cont{
    height: 92vh;
    width: 60%;
    overflow-y: auto;
}
.blog-cont::-webkit-scrollbar {
    display: none;
}
.blog-avatar-cont{
    width: 100%;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.blog-avatar-cont div{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.blog-avatar-cont div img{
    height: 64px;
    width: 64px;
}
.blog-avatar-cont div p{
    padding-left: 15px;
    font-size: large;
}
.blog-avatar-cont .quote{
    margin-top: 15px;
}
.blog-item-cont{
    /* border: 1px solid black; */
    padding: 15px;
    padding-left: 25px;
}
.blog-item{
    margin-top: 20px;
    /* border: 1px solid black; */
}
.blog-item-tweet{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 16px;
    width: 90%;
}
.blog-item-time p{
    width: 30%;
}
.blog-item-time{
    display: flex;
    width: 95%;
    justify-content: end;
    margin-top: 5px;
    border-bottom: 1px solid #000;
    margin-bottom: 5px;
    padding-bottom: 3px;
}

.blogLoading-cont{
    margin-top: 20px;
}
.blogLoading-tweet{
    width: 60%;
    height: 25px;
    border-radius: 10px;
}
.blogLoading-time p{
    width: 20%;
    height: 25px;
    border-radius: 10px;
}
.blogLoading-time{
    display: flex;
    width: 95%;
    justify-content: end;
    margin-top: 5px;
    margin-bottom: 5px;
    padding-bottom: 3px; 
}

.blogdLoading-image{
    height: 64px;
    width: 64px;
    border-radius: 100%;
}
.blogdLoading-username{
    height: 20px;
    width: 150px;
    margin-left: 15px;
    border-radius: 10px;
}
.bloadLoading-quote{
    height: 20px;
    width: 300px;
    margin-top: 15px;
    border-radius: 10px;
}

.blogSkel {
    opacity: .7;
    animation: BlogSkeleton-loading 1s linear infinite alternate;
  }
  
@keyframes BlogSkeleton-loading {
    0%{
    background-color: hsl(200, 20%, 80%);
  }

  100% {
    background-color: hsl(200, 20%, 90%);
  }
}

