*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.signup-cont{
    height: 100vh;
    width: 100%;
    background-color: #EFF5F5;
    display: flex;
    justify-content: center;
    align-items: center;
}
.signup-cont__inner{
    height: 460px;
    width: 500px;
    background-color: #FF8787;
    display: grid;
    grid-template-rows: 100px 60px 60px 60px 180px;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
}
.signup-cont__inner input{
    height: 40px;
    width: 330px;
    outline: 0;
    border: 1px solid rgba(0, 0, 0, 0.295);
    background-color: #EFF5F5;
    padding-left: 10px;
    font-size: 15px;
    border-radius: 4px;
}

.signup-title{
    font-size: 40px;
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    font-weight: 700;
}
.signup-cont__inner div:last-child{
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.signup-cont__inner div:last-child button{
    padding: 10px;
    font-size: 15px;
    background-color: white;
    border: 0;
    border-radius: 4px;
    text-decoration: inherit;
    color: black;
    width: 75px;
    height: 40px;
    margin: 25px 0px;
    cursor: pointer;
}
.signup-cont__inner div:last-child button:last-child{
    width: 180px;
    height: 40px;
}
.signup-cont__inner div:nth-child(4){
    position: relative;
}
.showPasswordToggle{
    position: absolute;
    top: 15%;
    right: 3%;
    z-index: 6;
}

.otp-cont{
    position: absolute;
    height: 150px;
    width: 250px;
    background-color: #FF8787;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.otp-cont input{
    height: 40px;
    width: 200px;
    outline: 0;
    border: 1px solid rgba(0, 0, 0, 0.295);
    background-color: #EFF5F5;
    padding-left: 10px;
    font-size: 15px;
    border-radius: 4px;
}

.otp-cont button{
    padding: 10px;
    font-size: 15px;
    background-color: rgb(238, 234, 234);
    border: 0;
    border-radius: 4px;
    text-decoration: inherit;
    color: black;
    width: 75px;
    height: 40px;
    margin-top: 10px;
    cursor: pointer;
}
.otpLoading-cont {
    display: grid;
    grid-template-rows: 300px 70px 100px;
}
.otpLoading-cont div{
    font-size: 25px;
    color: white;
    font-weight: 600;
}
.otpLoading-cont__image, .otpLoading-cont__tickmark{
    width: 120px;
}
.otpLoading-cont__image{
    margin-top: 40px;
}