*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    overflow-y: hidden;
    overflow-x: hidden;
}
.profile-cont{
    width: 85%;
    height: 92vh;
    overflow-y: auto;
}

.profile-cont::-webkit-scrollbar {
    width: 8px;
}
.profile-cont::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.1);
}
.profile-cont::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 9px #FF8787;
}

.profile-thought-cont{
    margin-top: 50px;
    height: 150px;
    width: 80%;
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    padding-right: 8%;
    margin-left: 10px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 6px, rgba(0, 0, 0, 0.1) 0px 3px 6px;
    margin-bottom: 10px;
}
.profile-thought-cont textarea{
    padding-top: 5px;
    padding-left: 9px;
    font-size: 16px;
    border: 1px solid white;
    width: 650px;
    outline: 0;
    resize: none;
    height: 110px;
    line-height: 20px;
}
.profile-thought-cont textarea:focus{
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    transition: all 100ms;
}

.profile-thought-cont button{
    height: 40px;
    width: 115px;
    border-radius: 5px;
    border: 0;
    background-color: #FF8787;
    color: white;
    padding: 5px 10px;
    font-size: 15px;
}

.profile-thought-cont div:last-child{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.editProfile-cont{
    height:350px;
    width: 80%;
    margin-left: 10px;
    margin-top: 10px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 6px, rgba(0, 0, 0, 0.1) 0px 3px 6px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 3%;
    position: relative;
}
.editProfile-cont__false{
    display: none;
}
.profile-blog-item-cont__true{
    display: none;
}
.editProfile-cont img{
    width: 90px;
}
.editProfile-cont input{
    border: 0;
    background-color: #ff87877b;
    font-size: 16px;
    height: 35px;
    width: 298px;
    border-radius: 2px;
    padding-left: 5px;
    border: 1px solid white;

}
.editProfile-cont textarea{
    border: 0;
    resize: none;
    border-radius: 2px;
    margin-top: 20px;
    font-size: 15px;
    background-color: #ff87877b;
    padding: 1px;
    border: 1px solid white;
}

.editProfile-cont textarea:focus, .editProfile-cont input:focus{
    outline: 0;
    border: 1px solid rgba(0, 0, 0, 0.262);
}

.edit-cont-footer{
    position: absolute;
    bottom: 2%;
    left: 10%;
}
.edit-cont-btns button{
    position: absolute;    
    bottom: 5%;
    right: 5%;
    border: 0;
    padding: 12px 20px;
    border-radius: 3px;
    font-size: 15px;
    color: white;
    background-color: #FF8787;
}
.editProfile-cont div:nth-child(2){
    width: 370px;
}
.edit-cont-btns button:last-child{
    right: 15%;
}
.editProfile-cont div:first-child{
    position: relative;
}
.editProfile-cont .file-picker{
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100px;
    cursor: pointer;
}
.profileUpdate-dialog{
    position: absolute;
    top: 10%;
    left: 40%;
    height: 90px;
    width: 250px;
    background-color: #FF8787;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: black;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding-left: 10px;
    border-radius: 5px;
    animation:profileUpdateDialogAnim 10s ;
}
.profileUpdate-dialog__false{
    display: none;
}

@keyframes profileUpdateDialogAnim{
    0%{
        opacity: 0;
        transform: translateY(-15px);
    }10%{
        opacity: 1;
        transform: translateY(0);    
    }12%{
        opacity: 0;
        transform: translateY(-15px);
    }
    100%{
        opacity: 0;
    }
}

.lds-ripple {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-ripple div {
    position: absolute;
    border: 4px solid black;
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  .lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
  }
  @keyframes lds-ripple {
    0% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    4.9% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    5% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 72px;
      height: 72px;
      opacity: 0;
    }
  }
  