*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
.sidebar-cont{
    width: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.sidebar-cont__inner{
    height: 85%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1.5rem;
    justify-content: space-around;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
    border-radius: 5px;
}

.user-avatar img{
    width: 80px;
    border-radius: 100%;
}
.sidebar-links {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 65%;
}

.sidebar-links__img{
    width: 35px;
}
.logo-anim__true{
    animation: FriendReqAnim 0.5s infinite ;
}

@keyframes FriendReqAnim{
    25%{
        transform: scale(1.1);
    }75%{
        transform: scale(1);
    }
}