*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.login-cont{
    height: 100vh;
    width: 100%;
    background-color: #EFF5F5;
    display: flex;
    justify-content: center;
    align-items: center;
}
.login-cont__inner{
    height: 400px;
    width: 500px;
    background-color: #FF8787;;
    display: grid;
    grid-template-rows: 100px 60px 60px 180px;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
}
.login-cont__inner input{
    height: 40px;
    width: 330px;
    outline: 0;
    border: 1px solid rgba(0, 0, 0, 0.295);
    background-color: #EFF5F5;
    padding-left: 10px;
    font-size: 15px;
    border-radius: 4px;
}

.login-title{
    font-size: 40px;
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    font-weight: 700;
}
.login-cont__inner div:last-child{
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.login-cont__inner div:last-child button{
    padding: 10px;
    font-size: 15px;
    background-color: white;
    border: 0;
    border-radius: 4px;
    text-decoration: inherit;
    color: black;
    width: 70px;
    height: 40px;
    margin: 25px 0px;
    cursor: pointer;
}
.login-cont__inner div:last-child button:last-child{
    width: 180px;
    height: 40px;
}
.login-cont__inner div:nth-child(3){
    position: relative;
}
.showPasswordToggle{
    position: absolute;
    top: 15%;
    right: 3%;
    z-index: 6;
}