*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    overflow-y: hidden;
}
.friendRec-cont{
    width: 85%;
    height: 92vh;
    padding: 50px;
    overflow-y: auto;
}

.friendRec-cont::-webkit-scrollbar {
    display: none;
}



.friendRec-results{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.friendRec-result-item{
    width: 300px;
    margin: 10px;
    display: flex;
    justify-content: start;
    background-color: #ff87874d;
    align-items: center;
    padding-left: 30px;
    height: 90px;
}
.friendRec-result-item img{
    width: 80px;
    border-radius: 100%;
}
.friendRec-result-item p{
    margin-left: 20px;
    font-size: 16px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.friendRec-result-item-logo img{
    width: 25px;
    margin-left: 40px;
    cursor: pointer;
}

.friendRec-result-item-logo{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: space-between;
}
.friendRec-result-item__acceptReq-btn{
    background-color: rgb(255, 255, 255);
    border: 0;
    padding: 4px ;
    width: 100px;
    border-radius: 4px;
    margin-top: 5px;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0.125);
}
.friendRec-result-item__acceptReq-btn:hover{
    background-color: #FF8787;
}
.friendRec-result-item-logo{
    margin-left: 15px;
}














.card{
    width: 300px;
    margin: 10px;
    display: flex;
    justify-content: start;
    background-color: #ff87874d;
    align-items: center;
    padding-left: 30px;
    height: 90px;
}

.card__title{
    margin-left: 10px;
    height: 20px;
    width: 80px;
    background-color: rgb(255, 255, 255);
}
.card-titleDes{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: space-between;
    margin-left: 15px;
}

.card__description{
    background-color: rgb(255, 255, 255);
    border: 0;
    padding: 4px ;
    width: 100px;
    height: 25px;
    border-radius: 4px;
    margin-top: 5px;
    cursor: pointer;
}

.skel {
    /* opacity: .7; */
    animation: skeleton-loading 1s linear infinite alternate;
  }

@keyframes skeleton-loading {
    0% {
      background-color: rgba(128, 128, 128, 0.1);
    }
  
    100% {
      background-color: rgba(128, 128, 128, 0.4);
    }
  }

  .card__image{
    width: 80px;
    border-radius: 100%;
    background-color: white;
    height: 80px;
}

.friendRec-cont__empty-inner{
    display: flex;
    width: 550px;
    justify-content: space-around;
    align-items: center;
    height: 100%;
}
.friendRec-cont__empty-inner img{
    height: 200px;
}
.friendRec-cont__empty-inner p{
    font-size: 30px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}