*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
.ProfileEditor-cont{
    width: 85%;
    height: 92vh;
    display: flex;
    align-items: center;
}
.ProfileEditor-cont__inner{
    width: 90%;
    height: 90%;
    overflow-y: auto;
}
.profileEdit-leftRight-holder{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.profile-previewImage{
    border: 1px solid rgba(0, 0, 0, 0.278);
    width: 500px;
    height: 200px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 10px;
}
.profile-previewImage span{
    font-size: 28px;
    margin-right: -50px;
    color: #FF8787;
}
.profile-previewImage img{
    width: 110px;
    height: 110px;
    border-radius: 50%;
    border: 4px solid #FF8787;
}
.arrow_left{
    width: 30px;
}
.profileEdit-leftcont{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 500px;
}

.ProfileEditor-cont__inner button{
    padding: 10px 20px;
    background-color: #FF8787;
    border: 0;
    color: white;
    font-size: 15px;
    border-radius: 5px;
}
.profileEdit-rightcont textarea{
    resize: none;
    height: 100px;
    width: 400px;
    padding: 5px;
    margin-top: 10px;
    font-size: 16px;
    letter-spacing: .5px;
    border: 1px solid rgba(0, 0, 0, 0.278);
}
.profileEdit-rightcont textarea:focus{
    outline: 0;border: 1px solid rgba(0, 0, 0, 1);
}
.profileEdit-rightcont{
    width: 400px;
}
.profileEdit-rightcont p{
    font-size: 30px;
    text-align: center;
    color: #FF8787;
}
.saveChanges-btn{
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-top: 2%;
}
.try-captions p{
    margin-top: 10px;
    text-align: left;
}
.try-captions div p{
    background-color: #fcabab;
    font-size: 16px;
    color: white;
    padding:8px;
}