*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    overflow-y: hidden;
}
.search-cont{
    width: 85%;
    height: 92vh;
    overflow-y: auto;
}

.search-cont::-webkit-scrollbar {
    width: 8px;
}
.search-cont::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.1);
}
.search-cont::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 9px #FF8787;
}







.search-cont__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.search-inp{
    height: 150px;
}
.search-inp div{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.search-inp div input{
    border: 1px solid rgba(0, 0, 0, 0.115);
    font-size: 15px;
    height: 35px;
    width: 270px;
    outline: 0;
    padding-left: 5px;
    background-color: rgba(0, 0, 0, 0.01);
    border-radius: 4px;
}
.search-inp div button{
    border: 0;
    background-color: #FF8787;;
    color: white;
    font-size: 15px;
    padding: 10px 20px;
    border-radius: 4px;
    margin-left: 10px;
    cursor: pointer;
}
.empty-line{
    height: 3px;
    background-color: #FF8787;
    width: 450px;
    border-radius: 20px;
    margin: -20px 0 30px 0;
}
.search-results{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.search-result-item{
    width: 300px;
    margin: 10px;
    display: flex;
    justify-content: start;
    background-color: #ff87874d;
    align-items: center;
    padding-left: 30px;
    height: 90px;
}
.search-result-item img{
    width: 80px;
    border-radius: 100%;
}
.search-result-item p{
    margin-left: 20px;
    font-size: 16px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.search-result-item .search-result-sendRequest{
    background-color: #FF8787;
    border: 0;
    padding: 5px ;
    width: 100px;
    border-radius: 4px;
    margin-top: 5px;
    cursor: pointer;
}
.search-result-item-logo{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: space-between;
    /* display: none; */
    margin-left:15px; 
}
.search-result-item .search-result-requestSent{
    background-color: rgb(221, 199, 199);
    border: 0;
    padding: 5px ;
    width: 100px;
    border-radius: 4px;
    margin-top: 5px;
    cursor: pointer;
}

.search-result-item .search-result-alreadyFriends{
    background-color: rgba(182, 232, 106, 0.6);
    border: 0;
    padding: 5px ;
    width: 100px;
    border-radius: 4px;
    margin-top: 5px;
    cursor: pointer;
    color: black;
}
