/* @import url('https://fonts.googleapis.com/css2?family=Holtwood+One+SC&display=swap'); */
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
.navbar-cont{
    height: 8vh;
    width: 100%;
    background-color: #FF8787;
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar-cont__inner{
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.title-cont{
    display: flex;
}
.title-cont img{
    width: 30px;
    height: 30px;
}
.navbar-title{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    font-size: 35px;
    font-weight: 700;
}
.logout-btn{
    border: 0;
    padding: 9px 60px;
    border-radius: 4px;
    font-size: 16px;
    background-color: white;
    cursor: pointer;
}
.logout-btn__false{
    display: none;
}
.logout-cont{
    display: flex;
    width: 170px;
    justify-content: space-between;
}
.logout-conf-btn1{
    border: 0;
    padding: 9px 25px;
    border-radius: 3px;
    font-size: 16px;
    background-color: white;
    cursor: pointer;
    animation: sure-btnAnim 100ms;
}
.logout-conf-btn2{
    border: 0;
    padding: 9px 28px;
    border-radius: 3px;
    font-size: 16px;
    background-color: white;
    cursor: pointer;
    animation: no-btnAnim 100ms;
}

.logout-conf-btn__false{   
    display: none;
}
@keyframes no-btnAnim{
    0%{
        transform: translateX(-10px);
    }
    100%{
        transform: translateX(0px);
    }
}
@keyframes sure-btnAnim{
    0%{
        transform: translateX(10px);
    }
    100%{
        transform: translateX(0px);
    }
}